import Vue from 'vue'
import Router from 'vue-router'
import RadarGallery from '@/components/radarGallery/radarGallery'
import RadarPetProfile from '@/components/radarPetProfile/radarPetProfile'
import RadarSections from '@/components/radarSections/radarSections'
import RadarUserProfile from '@/components/radarUserProfile/radarUserProfile'

Vue.component('radar-gallery', RadarGallery)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/gallery',
      name: 'RadarGallery',
      component: RadarGallery
    },

    {
      path: '/petProfile',
      name: 'petProfile',
      component: RadarPetProfile
    },

    {
      path: '/sections',
      name: 'RadarSections',
      component: RadarSections
    },

    {
      path: '/userProfile',
      name: 'UserProfile',
      component: RadarUserProfile
    }
  ]
})



