<template src="./App.template.pug" lang="pug"></template>

<script>
  export default {
    name: 'RadarApp',
    data () {
      return {
        isSidebarOpened: true,
        sidebarSections: [
          {
            title: 'Home'
          },
          {
            title: 'About'
          }
        ],
        mainSections: [
          {
            title: 'Galería',
            id: 'gallery',
            path: '/gallery'
          },
          {
            title: 'Dar en adopción',
            id: 'adoption',
            path: '/adoption'
          }
        ]
      }
    },
    methods: {
      navigateTo: function (index) {
        console.log('navegate', index)
        this.$router.push(this.mainSections[index].path)
      }
    }
  }
</script>

<style>

  .with.top-toolbar main > .content {
    padding-top: 0;
  }

  .md-tabs .md-tab {
    padding: 0;
  }

</style>



